.controlPanel {
  width: 90%;
  height: 100px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;  
}

.controlPanel input {
  border: 2px solid whitesmoke;
  background-color: black;
  max-width: 600px;
  width: 100%;
  height: 30px;
  color:whitesmoke;
  outline: none;
  border-radius: 10px;
}

.controlPanel button {
  border: 2px solid whitesmoke;
  background-color: black;
  max-width: 100px;
  width: 100%; 
  height: 35px;
  color:whitesmoke;
  outline: none;
  align-self: center;
  border-radius: 10px;  
}

@media screen and (max-width: 500px) {

 .controlPanel button {
     max-width: 60px;
     width: 100%; 
 }
}

.changeCardPosition {
  margin: 20px;
  border: 2px solid whitesmoke;
  background-color: black;
  max-width: 170px;
  width: 100%; 
  height: 35px;
  color:whitesmoke;
  outline: none;
  align-self: center;
  border-radius: 10px;  
}

.changeCardPosition:hover {
  color:black;
  background-color: gray;
  cursor: pointer;  
}

.headerBackground a {
  color:whitesmoke;
  text-decoration: none;
  outline: none;
  font-size: 30px;
  font-family:fantasy;
}

.headerBackground a:hover {
  color: darkgray;
}

.controlPanel button:hover {
  color:black;
  background-color: gray;
  cursor: pointer;  
}

.wrapper {
  max-width: 1150px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap; 
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 310px;
  height: 100%;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  margin: 10px;
  border-radius: 10px;
  background-color: linear-gradient(black,white);
}

.card p {
  overflow: auto;
  padding: 20px;
  height: 160px;
}

.card p::-webkit-scrollbar {
  width: 0 !important;
}
.cardToRow {
  display:flex;
  width: 100%;
  margin-top: 30px;
  justify-content: space-around;
  flex-direction: row-reverse;  
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  margin: 10px;
  /* overflow:auto; */
  padding: 20px;
  border-radius: 10px;
  background-color: linear-gradient(black,white);
}

.cardToRow h4 {
   text-align: center;
}


.cardTitleAndDescriptionHorizontalPosition {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.card h4 {
  text-decoration: none;
  color: black;
  outline: none;
  text-align: center;
  height: 40px;
}

.genres {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  height: 69px;
}

.card span {
  color:gray;
  margin: 7pX; 
}

.cardButtons {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.cardButtons button {
  border: 2px solid whitesmoke;
  background-color: black;
  width: 40%; 
  height: 25px;
  color:whitesmoke;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
  margin: 5px;
}

.cardButtons button:hover {
  color:black;
  background-color: whitesmoke;
  border: 2px solid black;
}

.cardButtons  a {
  font-size: 13px;
  font-family: sans-serif;
  border: 2px solid whitesmoke;
  background-color: black;  
  width: 40%; 
  height: 21px;
  color:whitesmoke;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  margin: 5px;
}

.cardButtons a:hover {
  color:black;
  background-color: whitesmoke;
  border: 2px solid black;
}

.image img {
  width: 310px;
  height: 300px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.infoWrapper {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;  
}

.infoImage { 
  width: 50%;
  display: flex;
  flex-direction: column;
  max-height: 550px;
  height: 100%;
  border-radius: 3%;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.infoImage img {
  border-radius: 3%;
}

.infoImage button {
  border: 2px solid whitesmoke;
  background-color: black;
  width: 100%; 
  height: auto;
  color:whitesmoke;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
  margin: 5px 0;
}

.infoImage button:hover {
  color:black;
  background-color: whitesmoke;
  border: 2px solid black;
}

.infoDetails {
  width: 45%;
  height: 100%;
  display: flex;
  padding: 5px;
  margin: 10px;
  flex-direction: column;
}

.infoDetails b {
  color:black;
}

.infoDetails span {
  color:gray;
  font-size: 25px;
}

@media screen and (max-width:800px){
  .infoImage {
      width: 90%;
      margin: 0 auto;
  }
  .infoImage img {
      border-radius: 0;
  }

  .infoDetails {
      width: 90%;
      margin: 0 auto;
      height: 100%;
      margin-bottom: 50px;
  }
}

.removeButton {
  border: 2px solid whitesmoke;
  background-color: black;
  width: 100%; 
  height: auto;
  color:whitesmoke;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
  margin: 5px 0;
}

.removeButton:hover {
  color:black;
  background-color: whitesmoke;
  border: 2px solid black;
}

.commentStyle {
  width: 100%;
  background-color: black;
  color:white;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  height: auto;
  overflow-wrap: break-word;
}